'use client'

import { createContext, useState, ReactNode } from "react";
import { Pizza } from "./components/Main";

type CarrinhoContextType = {
    carrinho: Pizza[];
    adicionarPizza: (pizza: Pizza) => void;
};

export const CarrinhoContext = createContext<CarrinhoContextType | undefined>(undefined);

export function CarrinhoProvider( {children} : {children: ReactNode}) {
    const [carrinho, setCarrinho] = useState<Pizza[]>([]);

    const adicionarPizza = (pizza: Pizza) => {
        console.log(pizza)
        setCarrinho ((prevCarrinho) => [...prevCarrinho, pizza]);
    };

    return (
        <CarrinhoContext.Provider value={{carrinho, adicionarPizza}}>
            {children}
        </CarrinhoContext.Provider>
    )
}